.meeting-container {
  width: 100%;
  height: 100%;
  background: #fff;
  .nav {
    height:70px;
    background:rgba(255,255,255,1);
    box-shadow:0px 3px 6px 0px rgba(0,0,0,0.15);
  }
}

.capitalize {
  text-transform: capitalize;
}

.table {
  color: #ffff;
  flex-direction: column;
  position: absolute;
  top: 10px;
  left: 10px;
  height: 100%;
  opacity: 0.9;
  text-shadow: black 0.1em 0.1em 0.2em;
  font-size: 10px;
  .video-frame {
    position: absolute;
    top: 0;
  }
  .main-title {
    line-height: 18px;
    font-size: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 14px;
  }
  .date, .detail {
    // font-size: 12px;
    // line-height: 14px;
    font-size: 12px;
    line-height: 14px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 16px;
  }
}

.field-label {
  // width: 64px;
  height: 22px;
  font-size: 16px;
  font-family: PingFangSC-Medium,PingFang SC;
  font-weight: 500;
  color: #333333;
  line-height: 22px;
  margin: 0;
  margin-top: 37px;
  min-width: 100px;
  margin-bottom: 14px;
}

.input {
  width:180px;
  height:40px;
  background:rgba(255,255,255,1);
  box-shadow:0px 2px 4px 0px rgba(0,0,0,0.2);
  border-radius:2px;
  border:1px solid rgba(204,204,204,1);
  box-sizing: border-box;
  padding: 10px;
  outline: none;
  &:disabled {
    width:180px;
    height:40px;
    background:rgba(248,248,248,1);
    border-radius:2px;
    border:1px solid rgba(204,204,204,1);
  }
}

.content-container {
  position: relative;
  top: 135px;
  display: flex;
  align-items: center;
  flex-direction: column;

  .video-list {
    display: flex;
    flex: 1 1;
    justify-content: space-between;
    min-width: 660px;
    position: relative;
    .player {
      width: 320px;
      height: 240px;
      background: rgba(241,243,244,1);
      border-radius: 2px;
      position: relative;
      background-position: center;
      background-repeat: no-repeat;
      background-image: url("../assets/video-holder.png");
    }
    .score-info {
      margin-top: 10px;
      width: 320px;
      height: 180px;
      background: rgba(0, 0, 0, 0.5);
      box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.5);
      border-radius: 4px;
      box-sizing: border-box;
      position: relative;
      .review {
        display: inline-block;
        min-width: 56px;
        min-height: 20px;
        max-width: 56px;
        max-height: 20px;
        border-radius: 4px;
        font-size: 14px;
        line-height: 20px;
        background: #ff9500;
        text-align: center;
      }
      .video-frame {
        img {
          width: 320px;
          height: 299px;
        }
      }
      .pass {
        display: inline-block;
        min-width: 56px;
        min-height: 20px;
        max-width: 56px;
        max-height: 20px;
        border-radius: 4px;
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        background:rgba(57,195,81,1);
      }
      .block {
        display: inline-block;
        min-width: 56px;
        min-height: 20px;
        max-width: 56px;
        max-height: 20px;
        border-radius: 4px;
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        background:rgba(255,59,48,1);
      }
    }
  }

  .form-container {
    display: flex;
    flex-direction: column;
    .item {
      display: flex;
    }
  }
}
